import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';

import Utils from '../../Utils';

import './index.css';

class CollectionMenu extends Component {
  static propTypes = {
    routes: PropTypes.array.isRequired,
    sidebar: PropTypes.bool
  }

  renderLinks = () => {
    const { routes } = this.props;
    const revRoutes = [...routes].reverse();

    return routes && routes.length > 0
    ?
    revRoutes.map((route, i) => {
      const sqFtLowArr = route.residences.map(res => {return parseInt(res.sq_ft_low, 10)});
      const sqFtHighArr = route.residences.map(res => {return parseInt(res.sq_ft_high, 10)});
      const sqFtLowNum = Math.min.apply(null, sqFtLowArr);
      const sqFtHighNum = Math.max.apply(null, sqFtHighArr);
      const beds = route.residences.map(res => {return (parseInt(res.beds, 10))});
      const bedsLow = Math.min.apply(null, beds);
      const bedsHigh = Math.max.apply(null, beds);
      const baths = route.residences.map(res => {return (parseInt(res.baths, 10))});
      const bathsLow = Math.min.apply(null, baths);
      const bathsHigh = Math.max.apply(null, baths);
      const garage = route.residences.map(res => {return (parseInt(res.garage, 10))});
      const garageLow = Math.min.apply(null, garage);
      const garageHigh = Math.max.apply(null, garage);
      const slug = route.name.toLowerCase();

      return (
        <li key={i} className="collmenu-item">
          <NavLink
          key={i}
          to={`${this.props.pathRoot}/${slug}/residence-1`}
          className={`collmenu-link ${this.props.sidebar ? 'sidebar-link' : null}`}
          exact={false}
          activeClassName="active">
            <img className="rendering" src={route.rendering.sizes.medium_large} alt="rendering"/>
            <h2 className={slug}>{route.name}</h2>
            <p className="type"><strong>{route.collection_type}</strong></p>
            <p>Approx. {Utils.sqFtToStr(sqFtLowNum)} &ndash; {Utils.sqFtToStr(sqFtHighNum)} Sq. Ft.</p>
            <p>{bedsLow} {bedsLow !== bedsHigh ? ' – '+ bedsHigh : null} Bedrooms</p>
            <p>{bathsLow} {bathsLow !== bathsHigh ? ' – '+ bathsHigh : null} Bathrooms</p>
            <p>{garageLow}{garageLow !== garageHigh ? '-'+ garageHigh : null}-Car Garage</p>
            <p>{route.collection_price}</p>
          </NavLink>
        </li>
      )
    })
    :
    null
  }

  sqFtToStr = (num) => {
    return num.toString().length > 3 ? num.toString().substring(0,1) + ',' + num.toString().substring(1,4) : num;
  }

  render() {
    const subLinks = this.renderLinks();

    return (
      <div id="collmenu-wrap" className={this.props.sidebar ? 'sidebar-wrap' : null}>
        <ul className={`collmenu ${this.props.sidebar ? 'sidemenu' : null}`}>
          {subLinks}
        </ul>
      </div>
    )
  }
}

export default CollectionMenu;



