import React, { Component } from 'react';
import { Route } from 'react-router-dom';

import CollectionMenu from '../../components/CollectionMenu';
import CollectionWrap from '../CollectionWrap';

import shapesImages from '../../img/shapes-images-2x.png';

import './index.css';

class Collections extends Component {
  constructor(props) {
    super(props);

    this.state = {
      collections: [],
      allCollections: [],
      activeSlide: 0,
      prevActiveSlide:0
    }
  }

  componentDidMount() {
    let collections = sessionStorage.getItem('collections');

    if (collections) {
      collections = JSON.parse(collections);
      const allCollections = collections.map(item => item.acf);
      this.setState({
        collections,
        allCollections
      });
    } else {
      fetch('https://lantana-at-beach-wp.gjtouchscreen.com/?rest_route=/acf/v3/collections')
      .then(response => {
        return response.json();
      })
      .then(data => {
        const allCollections = data.map(item => item.acf);
        this.setState({
          collections: data,
          allCollections
        });
        sessionStorage.setItem('collections', JSON.stringify(data));
      });
    }
  }

  renderRoutes = () => {
    const { allCollections } = this.state;

    return allCollections.length > 0
    ?
    allCollections.map((item, i) => {
      const slug = item.name.toLowerCase();
      return (
        this.props.location.pathname.includes(this.props.match.path + '/' + slug)
        ?
        (
          <Route
            key={i}
            path={'/collections/:collection'}
            render={itemProps => <CollectionWrap key={i} data={item} rotate={this.props.rotate} {...itemProps} />}
          />
        )
        : null
      )
    })
    :
    null
  }

  render() {
    const { allCollections } = this.state;
    const routes = this.renderRoutes();
    
    return (
      <div id="collections" className="main-container">
        {this.props.location.pathname === '/collections' ?
          <div className="all-collections">
            <img className="shapes-images" src={shapesImages} alt="people smiling" />
            <h1>The Collections</h1>
            <CollectionMenu routes={allCollections} pathRoot="/collections" />
          </div>
        : routes}
      </div>
    );
  }
}

export default Collections;



