const utils = {
  postData(url, data) {
    return fetch(url, {
      method: 'POST',
      body: data
    })
    .then((response) => {
      console.log('postData response: ', response);
      if (response.ok) {
        console.log('success');
      } else {
        console.log('error');
      }
      response.json();
    })
  },
  getData(url) {
    return fetch(url)
    .then(response => response.json())
  },
  sqFtToStr(num) {
    return num.toString().length > 3 ? num.toString().substring(0,1) + ',' + num.toString().substring(1,4) : num;
  }
}

export default utils;



