import React, { Component } from 'react';
import { Route } from 'react-router-dom';

import SubMenu from '../../components/SubMenu';
import ResidenceDetail from '../../components/ResidenceDetail';

import './index.css';

class CollectionWrap extends Component {

  renderRoutes = () => {
    const residences = this.props.data.residences;
    // const renderings = this.props.data.residences.map(item => item.image);
    console.log('pathName: ', this.props.location.pathname);
    return residences.length > 0
    ?
    residences.map((item, i) => {
      const slug = this.props.data.name.toLowerCase();
      return (
        this.props.location.pathname === ('/collections/' + slug + '/residence-' + item.residence)
        ?
        (
          <Route
            key={i}
            path={`/collections/${slug}/:residence`}
            render={() => <ResidenceDetail key={i} residence={item} hood={this.props.data.name} slug={slug} rotate={this.props.rotate} />}
          />
        )
        : null
      )
    })
    :
    null
  }

  render() {
    const routes = this.renderRoutes();
    const slug = this.props.data.name.toLowerCase();
    console.log('routes: ', routes);

    return (
      <div className={`collection ${slug}`}>
        {routes}
        <SubMenu routes={this.props.data.residences} color={this.props.data.color} header={slug} pathRoot={'/collections/'+slug} />
      </div>
    )
  }
}

export default CollectionWrap;



