import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Carousel from 'nuka-carousel';
import Modal from 'react-modal';

import SidebarBottom from '../../img/sidebar-bottom.gif';
import One from '../../img/number-one.svg';
import Two from '../../img/number-two.svg';
import Three from '../../img/number-three.svg';
import ChevronIcon from '../icons/ChevronIcon';
import PlusIcon from '../icons/PlusIcon';
import CloseIcon from '../icons/CloseIcon';

import './index.css';

class ResidenceDetail extends Component {
  static propTypes = {
    residence: PropTypes.object.isRequired,
    // renderings: PropTypes.array.isRequired,
    hood: PropTypes.string.isRequired,
    slug: PropTypes.string.isRequired
  }

  constructor(props) {
    super(props);

    this.state = {
      modalIsOpen: false,
      currentSlide: 0
    };
  }

  renderRenderings = () => {
    const { residence } = this.props;
    const renderings = residence.renderings;

    return renderings && renderings.length > 1
    ?
    (
      <Carousel
        className="renderings"
        slidesToShow={1}
        slidesToScroll={1}
        swiping
        wrapAround
        renderCenterLeftControls={({ previousSlide }) => (
          <button className="residence-arrow left" onClick={previousSlide}>{ChevronIcon}</button>
        )}
        renderCenterRightControls={({ nextSlide }) => (
          <button className="residence-arrow" onClick={nextSlide}>{ChevronIcon}</button>
        )}
        renderBottomCenterControls={() => (
          <div></div>
        )}
      >
        {renderings.map((rendering, i) => 
          (
            <div key={i} className="rendering">
              <img className="rendering-img" src={rendering.url} alt={rendering.description} />
              <p className="rendering-caption">{rendering.caption}</p>
            </div>
          )
        )}
      </Carousel>
    )
    :
    renderings.map((rendering, i) => 
      (
        <div key={i} className="rendering">
          <img className="rendering-img" src={rendering.url} alt={rendering.description} />
          <p className="rendering-caption">{rendering.caption}</p>
        </div>
      )
    )
  }

  renderNumber = () => {
    const { residence } = this.props;

    return(
      residence.beds === '1'
      ?
      <img className="number" src={One} alt="1" />
      :
      residence.beds === '2'
      ?
      <img className="number" src={Two} alt="2" />
      :
      residence.beds === '3'
      ?
      <img className="number" src={Three} alt="3" />
      :
      null
    )
  }

  renderFloorPlans = () => {
    const { residence } = this.props;

    return (
      <div className={`floor-plans residence-${residence.residence}`}>
        {residence.floor_plans.map((plan, i) => {
          // const heightClass = plan.height > plan.width * 1.58 ? 'tall' : null;
          return (
            <div className="plan" key={i}>
              <img src={plan.url} alt={plan.caption} />
              <p>{plan.caption}</p>
            </div>
          )
        })}
      </div>
    )
  }

  handleOpenModal = () => {
    this.setState({ modalIsOpen: true });
  }

  handleCloseModal = () => {
    this.setState({modalIsOpen: false});
  }

  render() {
    const { residence, hood } = this.props;
    const { modalIsOpen } = this.state;
    const number = this.renderNumber();
    const renderings = this.renderRenderings();
    const floorPlans = this.renderFloorPlans();

    return (
      <div id="residence-detail" className={`container ${hood}`}>
        <div id="sidebar-wrap" className={`beds-${residence.beds}`}>
          <div id="sidebar">
            <div className="hood">{hood}</div>
            <div className="big-bedroom">
              {number}
              <div className="label">Bedroom{parseInt(residence.beds, 10) > 1 ? 's' : null}</div>
            </div>
            <div class="details">
              <div className="detail residence">Residence {residence.residence}</div>
              <div className="detail sqft">Approx. {residence.sq_ft_low} Sq. Ft.</div>
              <div className="detail beds">{residence.beds} Bed{parseInt(residence.beds, 10) > 1 ? 's' : null}</div>
              <div className="detail baths">{residence.baths} Bath{parseInt(residence.baths, 10) > 1 ? 's' : null}</div>
              {residence.covered_deck ? <div className="detail deck">Covered Deck</div> : null}
              <div className="detail garage">{residence.garage}-Car Garage</div>
            </div>
          </div>
          <img className="sidebar-bottom" src={SidebarBottom} aria-hidden="true" alt="" />
        </div>
        <div className="rendering-wrap">
          {renderings}
          <div className="click-to-enlarge" onClick={this.handleOpenModal}>{PlusIcon}</div>
        </div>
        {floorPlans}
        <Modal isOpen={modalIsOpen} className="modal-content" contentLabel="Rendering Modal" ariaHideApp={false} onRequestClose={this.handleCloseModal}>
          <button onClick={this.handleCloseModal} className="close">{CloseIcon}</button>
          {renderings}
        </Modal>
      </div>
    )
  }
}

export default ResidenceDetail;



