import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';

import Utils from '../../Utils';

// import ChevronIcon from '../../components/icons/ChevronIcon';
import printIcon from '../../img/homefinder/print.svg';

import './index.css';

class SubMenu extends Component {
  static propTypes = {
    routes: PropTypes.array.isRequired,
    sidebar: PropTypes.bool
  }

  constructor(props) {
    super(props);

    this.state = {
      beds: ''
    }
  }

  renderLinks = () => {
    const { routes } = this.props;

    return routes && routes.length > 0
    ?
    routes.map((route, i) => {
      return (
        <li key={i} className={`submenu-item ${this.props.sidebar ? 'sidebar-item' : null}`}>
          <NavLink
          key={i}
          to={`${this.props.pathRoot}/residence-${route.residence}`}
          className={`submenu-link ${this.props.sidebar ? 'sidebar-link' : null} ${route.powders > 0 ? 'powders' : null}`}
          exact={false}
          activeClassName="active">
            Residence {route.residence}
            {/* <ul className="sub-submenu">
              <li>{route.sq_ft_low === route.sq_ft_high ? Utils.sqFtToStr(route.sq_ft_high) : Utils.sqFtToStr(route.sq_ft_low) + ' – ' + Utils.sqFtToStr(route.sq_ft_high)} Sq. Ft.</li>
              <li>{route.beds} Bedroom{route.beds > 1 ? 's' : null}</li>
              <li>{route.baths} Bathroom{route.baths > 1 ? 's' : null}</li>
              {route.powders > 0 ? <li>{route.powders} Powder Room{route.powders > 1 ? 's' : null}</li> : null}
              <li>{route.garage}-Car Garage</li>
            </ul> */}
          </NavLink>
        </li>
      )
    })
    :
    null
  }

  printFloorplans = (e) => {
    e.preventDefault();
    window.print();
  }

  render() {
    const subLinks = this.renderLinks();
    const bgStyle = {
      background: this.props.color
    };
    console.log('submenu props: ', this.props);

    return (
      <div id="submenu-wrap" className={this.props.sidebar ? 'sidebar-wrap' : null} style={bgStyle}>
        {/* <h1>{this.props.header}</h1> */}
        <ul className={`submenu ${this.props.sidebar ? 'sidemenu' : null}`}>
          {subLinks}
           {/* <li className={`submenu-item ${this.props.sidebar ? 'sidebar-item' : null}`}>
            <button id="print-floorplans" onClick={(e) => this.printFloorplans(e)}>
              <img className="print-icon" src={printIcon} alt=""/>
              <span>Print Floor Plans</span>
            </button>
          </li> */}
        </ul>
      </div>
    )
  }
}

export default SubMenu;



