import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Select from 'react-select';

import Residence from '../../components/ResidenceResult';
import ChevronIcon from '../../components/icons/ChevronIcon';

import Carousel from 'nuka-carousel';

import itsNewItsYou from '../../img/its-new-its-you.svg';
import shapes01 from '../../img/shapes-01.svg';
import shapesCorner from '../../img/shapes-corner.svg';

import loadingIcon from '../../img/homefinder/loading-white.gif';
import mailIcon from '../../img/homefinder/mail.svg';
import printIcon from '../../img/homefinder/print.svg';

import './index.css';

const budgetOptions = [
  { value: '395000-550000', label: '$395K – $550K' },
  { value: '550001-650000', label: '$551K – $650K' },
  { value: '650001-750000', label: '$651K – $750K' },
  { value: '750001-850000', label: '$751K – $850K' },
  { value: '850001-1200000', label: '$851K – $1.2M' }
];

const bedsOptions = [
  { value: '1', label: 'Just me (1 bedroom)' },
  { value: '2', label: 'Small family or large shoe collection (2 bedrooms)' },
  { value: '3', label: 'Larger family & maybe a dog (3 bedrooms)' },
  { value: '4', label: 'Everything (4 bedrooms)' }
];

const bathsOptions = [
  { value: '1', label: '1 is plenty' },
  { value: '2', label: '2 is better' },
  { value: '3', label: 'I can never have too many bathrooms (3+)' }
];

// const outdoorOptions = [
//   { value: 'yard', label: 'Sit on the grass (yard)' },
//   { value: 'covered-deck', label: 'Enjoy fresh air (covered deck)' },
//   { value: 'roof-deck', label: 'Stargaze (rooftop deck)' }
// ];

// const firstFloorOptions = [
//   { value: 'flexroom', label: 'Extra space for activities' },
//   { value: 'bedroom', label: 'A bedroom for family' }
// ];

const win = window.innerWidth;
const slideShowNum = win < 500 ? 1 : win >= 500 && win < 667 ? 2 : win >= 667 && win < 960 ? 3 : win >= 960 && win < 1060 ? 4 : win >= 1060 && win < 1280 ? 5 : 6;

class HomeFinder extends Component {
  static propTypes = {
    anchors: PropTypes.bool
  }

  constructor(props) {
    super(props);
    this.state = {
      currentSlide: 0,
      showResults: false,
      showResMsg: false,
      showRelMsg: false,
      budget: 'choose',
      budgetOptionIndex: null,
      beds: 'choose',
      bedsOptionIndex: null,
      baths: 'choose',
      bathsOptionIndex: null,
      outdoor: 'choose',
      outdoorOptionIndex: null,
      firstFloorSpace: 'choose',
      firstFloorOptionIndex: null,
      residences: [],
      results: [],
      matches: null,
      loading: false,
      showEmailResults: false,
      sending: 'no',
      email: '',
      error: false,
      errorMessage: ''
    };
  }

  componentDidMount() {
    const residences = sessionStorage.getItem('residences');
    const collections = sessionStorage.getItem('collections');
    const emailResults = sessionStorage.getItem('showEmailResults');
    const sendingState = sessionStorage.getItem('sending');

    if (residences) {
      const residencesData = JSON.parse(residences);
      this.setState({ residences: residencesData });
      this.setResultsData(residencesData);
      this.setFormData();
    } else if (collections) {
        const setRes = this.setResidences(collections);
        this.setState({ residences: setRes });
        this.setResultsData(setRes);
        this.setFormData();
        sessionStorage.setItem('residences', JSON.stringify(setRes));
    } else {
      fetch('https://lantana-at-beach-wp.gjtouchscreen.com/?rest_route=/acf/v3/collections')
      .then(response => {
        return response.json();
      })
      .then(data => {
        const setRes = this.setResidences(data);
        this.setState({ residences: setRes });
        this.setResultsData(setRes);
        this.setFormData();
        sessionStorage.setItem('collections', JSON.stringify(data));
        sessionStorage.setItem('residences', JSON.stringify(setRes));
      });
    }
    if (emailResults) {
      this.setState({ showEmailResults: emailResults });
    }
    if (sendingState) {
      this.setState({ sending: sendingState });
    } else {
      sessionStorage.setItem('sending', this.state.sending);
    }
  }

  setFormData = () => {
    const storedBudget = sessionStorage.getItem('budget'),
          storedBeds = sessionStorage.getItem('beds'),
          storedBaths = sessionStorage.getItem('baths'),
          storedOutdoor = sessionStorage.getItem('outdoor'),
          storedFirstFloorSpace = sessionStorage.getItem('firstFloorSpace'),
          storedBudgetOptionIndex = sessionStorage.getItem('budgetOptionIndex'),
          storedBedsOptionIndex = sessionStorage.getItem('bedsOptionIndex'),
          storedBathsOptionIndex = sessionStorage.getItem('bathsOptionIndex'),
          // storedOutdoorOptionIndex = sessionStorage.getItem('outdoorOptionIndex'),
          // storedFirstFloorOptionIndex = sessionStorage.getItem('firstFloorOptionIndex'),
          storedMatches = sessionStorage.getItem('matches');

    storedBudget ? this.setState({ budget: storedBudget }) : sessionStorage.setItem('budget', 'choose');
    storedBeds ? this.setState({ beds: storedBeds }) : sessionStorage.setItem('beds', 'choose');
    storedBaths ? this.setState({ baths: storedBaths }) : sessionStorage.setItem('baths', 'choose');
    storedOutdoor ? this.setState({ outdoor: storedOutdoor }) : sessionStorage.setItem('outdoor', 'choose');
    storedFirstFloorSpace ? this.setState({ firstFloorSpace: storedFirstFloorSpace }) : sessionStorage.setItem('firstFloorSpace', 'choose');
    storedBudgetOptionIndex !== 'null' ? this.setState({ budgetOptionIndex: storedBudgetOptionIndex }) : sessionStorage.setItem('budgetOptionIndex', null);
    storedBedsOptionIndex !== 'null' ? this.setState({ bedsOptionIndex: storedBedsOptionIndex }) : sessionStorage.setItem('bedsOptionIndex', null);
    storedBathsOptionIndex !== 'null' ? this.setState({ bathsOptionIndex: storedBathsOptionIndex }) : sessionStorage.setItem('bathsOptionIndex', null);
    // storedOutdoorOptionIndex !== 'null' ? this.setState({ outdoorOptionIndex: storedOutdoorOptionIndex }) : sessionStorage.setItem('outdoorOptionIndex', null);
    // storedFirstFloorOptionIndex !== 'null' ? this.setState({ firstFloorOptionIndex: storedFirstFloorOptionIndex }) : sessionStorage.setItem('firstFloorOptionIndex', null);
    storedMatches ? this.setState({ matches: JSON.parse(storedMatches) }) : sessionStorage.setItem('matches', null);
  }

  setResultsData = (data) => {
    const storedResults = sessionStorage.getItem('results');
    if (storedResults) {
      const results = JSON.parse(storedResults);
      this.setState({ results });
    } else {
      this.setState({ results: data });
    }
  }

  setResidences = (data) => {
    let arr = [];
    console.log('data: ', data);

    data.map(item => {
      return item.acf.residences.map(r => {
        const budgetArr = item.acf.collection_price.split(' $');
        // const bQ = budgetArr[0].split('From the ')[1];
        // const budget = budgetArr[1].replace(',000s','000');
        // console.log('r.price: ', r.price);
        const priceArr = r.price.split('$');
        // console.log('priceArr: ', priceArr);
        const price = r.price.includes('$') ? priceArr[1].replace(/,/g, '') : 0;
        const rendering = r.renderings[0] ? r.renderings[0].sizes.medium : null;
        let res = {};
        res.collection = item.acf.name;
        res.color = item.acf.color;
        res.logo = item.acf.collection_logo.url;
        res.logo_width = item.acf.collection_logo.width;
        res.logo_height = item.acf.collection_logo.height;
        res.residence = r.residence;
        // res.budget_qualifier = bQ;
        res.budget = parseInt(budgetArr[1], 10);
        res.price = r.price;
        res.priceInt = parseInt(price, 10);
        res.beds = parseInt(r.beds, 10);
        res.baths = parseInt(r.baths, 10);
        res.powders = parseInt(r.powders, 10);
        res.sqft_low = parseInt(r.sq_ft_low.replace(',',''), 10);
        res.sqft_high = parseInt(r.sq_ft_high.replace(',',''), 10);
        res.covered_deck = r.covered_deck;
        res.roof_deck = r.roof_deck;
        res.yard = r.yard;
        res.first_floor_room = r.first_floor_room;
        res.rendering = rendering;
        arr.push(res);
        return null;
      });
    });
    return arr;
  }

  handleInputChange = (name, option, optionsArr, itemOptionIndex) => {
    const key = name,
          value = option.value

    const options = optionsArr.map(item => item.value);
    const optionIndex = options.indexOf(value);
    
    sessionStorage.setItem(key, value);
    sessionStorage.setItem(itemOptionIndex, optionIndex);
    this.setState({
      [key]: value,
      [itemOptionIndex]: optionIndex
    });
    this.filterData(key, value);
  }

  renderResults = (items) => {
    const { anchors } = this.props;
    const { currentSlide } = this.state;

    return items && items.length > 0
    ?
    (
      <Carousel
        className={items.length < slideShowNum + 1 ? 'fewer' : ''}
        slidesToShow={slideShowNum}
        slidesToScroll={1}
        slideWidth="190px"
        swiping={false}
        dragging={false}
        easing="easeQuadOut"
        cellSpacing={8}
        slideIndex={currentSlide}
        disableKeyboardControls={true}
        renderCenterLeftControls={() => (
          <div></div>
        )}
        renderCenterRightControls={() => (
          <div></div>
        )}
        renderBottomCenterControls={() => (
          <div></div>
        )}
      >
        {
          items.map((item, i) => {
          return (
            <Residence
              key={i}
              collection={item.collection}
              residence={item.residence}
              price={item.price}
              sqftLow={item.sqft_low}
              sqftHigh={item.sqft_high}
              beds={item.beds}
              budgetQualifier={item.budget_qualifier}
              budget={item.budget}
              rendering={item.rendering}
              useAnchors={anchors}
              index={i}
              handleRemoveItem={this.handleRemoveItem}
            />
          )
        })
      }
      </Carousel>
    )
    :
    <div id="loading">
      <img src={loadingIcon} alt="loading" />
    </div>
  }

  handleRemoveItem = (item) => {
    const { results } = this.state;
    let newResults = results.filter((result, i, self) => {
      // console.log('self.indexOf(result): ', self.indexOf(result));
      return self.indexOf(result) !== item;
    });

    // console.log('newResults: ', newResults);
    this.setState({ results: newResults });
    sessionStorage.setItem('newResults', JSON.stringify(newResults));
  }

  filterData = (changedName, changedValue) => {

    this.setState({
      showEmailResults: true,
      sending: 'no'
    });
    sessionStorage.setItem('showEmailResults', true);
    sessionStorage.setItem('sending', 'no');

    const budget = changedName === 'budget' ? changedValue : this.state.budget,
          beds = changedName === 'beds' ? changedValue : this.state.beds,
          baths = changedName === 'baths' ? changedValue : this.state.baths;
          // outdoor = changedName === 'outdoor' ? changedValue : this.state.outdoor,
          // firstFloorSpace = changedName === 'firstFloorSpace' ? changedValue : this.state.firstFloorSpace;

    const { residences } = this.state;

    let budgetItems = [];

    let items = residences.filter(item => {
      const budgetArr = budget.split('-'),
            budgetPass = budget !== 'choose' ? this.isInRange(item.priceInt, parseInt(budgetArr[0], 10), parseInt(budgetArr[1], 10)) : item.priceInt,
            bedsPass = beds !== 'choose' ? beds <= item.beds : item.beds,
            bathsPass = baths !== 'choose' ? baths <= item.baths : item.baths;
            // outdoorCondition01 = outdoor === 'covered-deck' && item.covered_deck,
            // outdoorCondition02 = outdoor === 'roof-deck' && item.roof_deck,
            // outdoorCondition03 = outdoor === 'yard' && item.yard,
            // outdoorPass = outdoor !== 'choose' ? (outdoorCondition01 || outdoorCondition02 || outdoorCondition03) : item.covered_deck ? item.covered_deck : item.roof_deck ? item.roof_deck : item.yard ? item.yard : item,
            // firstFloorPass = firstFloorSpace !== 'choose' ? firstFloorSpace === item.first_floor_room : item.budget;
  
      return (
        budgetPass ? budgetItems.push(item) : null,
        budgetPass && bedsPass && bathsPass
      );
    });

    if (items.length > 0) {
      this.setState({
        matches: true,
        results: items,
        currentSlide: 0
      },
        sessionStorage.setItem('results', JSON.stringify(items)),
        sessionStorage.setItem('matches', true)
      );
    } else if (budgetItems.length > 0) {
      this.setState({
        matches: false,
        results: budgetItems,
        currentSlide: 0
      },
        sessionStorage.setItem('results', JSON.stringify(budgetItems)),
        sessionStorage.setItem('matches', false)
      );
    } else {
      this.setState({
        matches: false,
        results: residences,
        currentSlide: 0
      },
        sessionStorage.setItem('results', JSON.stringify(residences)),
        sessionStorage.setItem('matches', false)
      );
    }
    
  }

  isInRange = (value, rangeLow, rangeHigh) => {
    return (value >= rangeLow && value <= rangeHigh);
  }

  renderResNum = () => {
    const { residences, results, matches } = this.state;

    if (residences && results) {
      return results.length === residences.length
      ?
      'all'
      :
      (results.length > 0 && matches)
      ?
      (results.length.toString() + ' matching')
      :
      results.length.toString();
    }
  }

  renderCollNum = () => {
    const { results } = this.state;
    const collArr = results ? results.map(r => r.collection) : null;
    const collNum = [...new Set(collArr)];

    return collNum.length > 0 ? ('from ' + collNum.length + ' collection' + (collNum.length > 1 ? 's' : '')) : '';
  }

  setActiveClass = (stateItem) => {
    return stateItem !== 'choose' ? 'active' : '';
  }

  setOptionIndexClass = (stateItem) => {
    return stateItem !== null ? `option-${stateItem}` : '';
  }

  handleChange(e) {
    e.preventDefault();

    const name = e.target.name;
    const value = e.target.value;

    this.setState({
      [name]: value
    });
  }

  renderEmailResults = () => {
    const firstName = sessionStorage.getItem('firstName');
    const emailAddress = sessionStorage.getItem('emailAddress');
    const results = sessionStorage.getItem('results');
    const newResults = sessionStorage.getItem('newResults');
    const { sending } = this.state;

    return (
      <form id="send-results" method="post">
        <input className="hidden" id="firstName" type="text" name="firstName" value={firstName ? firstName : ''} readOnly />
        <input className="hidden" id="emailAddress" type="text" name="emailAddress" value={emailAddress ? emailAddress : this.state.email} readOnly />
        <input className="hidden" id="homefinderResults" type="text" name="homefinderResults" value={newResults ? newResults : results ? results : ''} readOnly />
        <div id="sending">
            {
              sending === 'no'
              ?
              <div id="results-buttons">
                <div id="email-results-form">
                  {!emailAddress
                    ?
                    <input className="form-control" id="field2" type="text" name="email" placeholder="Email Address*" value={this.state.email} onChange={(e) => this.handleChange(e)} required="required" />
                    :
                    null
                  }
                  <button id="email-results" onClick={(e) => this.sendResults(e)}>
                    <img className="mail-icon" src={mailIcon} alt=""/>
                    <span>Email me my results</span>
                    <div className="chevron-icon">{ChevronIcon}</div>
                  </button>
                  <button id="print-results" onClick={(e) => this.printResults(e)}>
                    <img className="print-icon" src={printIcon} alt=""/>
                    <span>Print my results</span>
                    <div className="chevron-icon">{ChevronIcon}</div>
                  </button>
                </div>
                <div id="emailError">{this.state.errorMessage}</div>
                <p id="reset" onClick={this.reset}>Reset</p>
              </div>
              :
              sending === 'yes'
              ?
              <img className="loading" src={loadingIcon} alt="loading" />
              :
              sending === 'done'
              ?
              <p>Your results have been sent.</p>
              :
              null
            }
          </div>
      </form>
    )
  }

  sendResults = (e) => {
    e.preventDefault();
    const firstName = document.getElementById('firstName').value;
    const emailAddress = document.getElementById('emailAddress').value;
    const homefinderResults = document.getElementById('homefinderResults').value;
    const data = 'firstName='+firstName+'&emailAddress='+emailAddress+'&homefinderResults='+homefinderResults;

    if (emailAddress.match(/^([^@\s]+)@((?:[-a-z0-9]+\.)+[a-z]{2,})$/i) === null) {
      this.setState({
        error: true,
        errorMessage: 'Please enter a valid email address.'
      });
    } else {
      this.setState({
        error: false,
        errorMessage: '',
        submitting: true
      });

      this.setState({ sending: 'yes' });

      fetch('https://novelpark.lyonhomes.com/content/themes/gj-boilerplate/ts-results.php', {
        method: 'POST',
        mode: 'no-cors',
        headers:{
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: data
      })
      .then((response) => {
        console.log('fetch response: ', response);
        if (response.type === 'opaque') {
          console.log('success');
          this.setState({
            sending: 'done'
          });
          sessionStorage.setItem('sending', 'done');
        } else {
          console.log('error');
        }
        return response;
      });
    }
  }

  printResults = (e) => {
    e.preventDefault();
    window.print();
  }

  reset = () => {
    sessionStorage.clear();
    sessionStorage.setItem('budget', 'choose');
    sessionStorage.setItem('beds', 'choose');
    sessionStorage.setItem('baths', 'choose');
    // sessionStorage.setItem('outdoor', 'choose');
    // sessionStorage.setItem('firstFloorSpace', 'choose');
    sessionStorage.setItem('results', JSON.stringify(this.state.residences));
    sessionStorage.setItem('matches', 'false');
    this.setState({
      currentSlide: 0,
      showResults: false,
      showResMsg: false,
      showRelMsg: false,
      budget: 'choose',
      budgetOptionIndex: null,
      beds: 'choose',
      bedsOptionIndex: null,
      baths: 'choose',
      bathsOptionIndex: null,
      // outdoor: 'choose',
      // outdoorOptionIndex: null,
      // firstFloorSpace: 'choose',
      // firstFloorOptionIndex: null,
      results: this.state.residences,
      matches: false,
      loading: false,
      showEmailResults: false
    });
  }

  nextSlide = () => {
    const { currentSlide, results } = this.state;
    return (
      (results.length - currentSlide) > slideShowNum ? this.setState({ currentSlide: currentSlide + 1 }) : null
    )
  }

  prevSlide = () => {
    const { currentSlide } = this.state;
    return (
      currentSlide > 0 ? this.setState({ currentSlide: currentSlide - 1 }) : null
    )
  }

  render() {
    const {
      currentSlide,
      budget,
      budgetOptionIndex,
      beds,
      bedsOptionIndex,
      baths,
      bathsOptionIndex,
      // outdoor,
      // outdoorOptionIndex,
      // firstFloorSpace,
      // firstFloorOptionIndex,
      results,
      residences,
      matches,
      showEmailResults } = this.state;
    const showResults = this.renderResults(results);
    const resNum = this.renderResNum();
    const collNum = this.renderCollNum();
    const resMsgClass = 'show';
    const relMsgClass = matches ? '' : results.length === residences.length ? '' : 'show';
    const emailResults = this.renderEmailResults();
    const prevClass = currentSlide === 0 ? 'disabled' : '';
    const nextClass = (results.length - currentSlide) <= slideShowNum ? 'disabled' : '';

    return (
      <div id="homeFinder">
        <img className="top-shapes" src={shapes01} alt="shapes" aria-hidden="true" />
        <img className="corner-shapes" src={shapesCorner} alt="shapes" aria-hidden="true" />
        <div id="top-content">
          <img className="new-you" src={itsNewItsYou} alt="It's New, It's You"/>
          <form id="homeFinderForm">
            <div className="form-group">
            <label htmlFor="budget">BUDGET:</label>
              <Select
                className={`select-wrapper budget ${this.setActiveClass(budget)} ${this.setOptionIndexClass(budgetOptionIndex)}`}
                classNamePrefix="select"
                name={budget}
                options={budgetOptions}
                placeholder={budgetOptionIndex !== null ? budgetOptions[budgetOptionIndex].label : ''}
                value={budget}
                onChange={value => this.handleInputChange('budget', value, budgetOptions, 'budgetOptionIndex')}
                isSearchable={false}
              />
            </div>
            <div className="form-group">
              <label htmlFor="beds">BEDROOMS:</label>
              <Select
                className={`select-wrapper beds ${this.setActiveClass(beds)} ${this.setOptionIndexClass(bedsOptionIndex)}`}
                classNamePrefix="select"
                name={beds}
                options={bedsOptions}
                placeholder={bedsOptionIndex !== null ? bedsOptions[bedsOptionIndex].label : ''}
                value={beds}
                onChange={value => this.handleInputChange('beds', value, bedsOptions, 'bedsOptionIndex')}
                isSearchable={false}
              />
            </div>
            <div className="form-group">
              <label htmlFor="baths">BATHROOMS:</label>
              <Select
                className={`select-wrapper baths ${this.setActiveClass(baths)} ${this.setOptionIndexClass(bathsOptionIndex)}`}
                classNamePrefix="select"
                name={baths}
                options={bathsOptions}
                placeholder={bathsOptionIndex !== null ? bathsOptions[bathsOptionIndex].label : ''}
                value={baths}
                onChange={value => this.handleInputChange('baths', value, bathsOptions, 'bathsOptionIndex')}
                isSearchable={false}
              />
            </div>
            {/* <div className="form-group">
              <label htmlFor="outdoor">After work, I like to:</label>
              <Select
                className={`select-wrapper outdoor ${this.setActiveClass(outdoor)} ${this.setOptionIndexClass(outdoorOptionIndex)}`}
                classNamePrefix="select"
                name={outdoor}
                options={outdoorOptions}
                placeholder={outdoorOptionIndex !== null ? outdoorOptions[outdoorOptionIndex].label : ''}
                value={outdoor}
                onChange={value => this.handleInputChange('outdoor', value, outdoorOptions, 'outdoorOptionIndex')}
                isSearchable={false}
              />
            </div>
            <div className="form-group">
              <label htmlFor="firstFloorSpace">On the ground floor, I'll need:</label>
              <Select
                className={`select-wrapper first-floor-space ${this.setActiveClass(firstFloorSpace)} ${this.setOptionIndexClass(firstFloorOptionIndex)}`}
                classNamePrefix="select"
                name={firstFloorSpace}
                options={firstFloorOptions}
                placeholder={firstFloorOptionIndex !== null ? firstFloorOptions[firstFloorOptionIndex].label : ''}
                value={firstFloorSpace}
                onChange={value => this.handleInputChange('firstFloorSpace', value, firstFloorOptions, 'firstFloorOptionIndex')}
                isSearchable={false}
              />
            </div> */}
          </form>
        </div>
        <div id="results-wrapper">
          <p id="related-message" className={relMsgClass}>Nothing quite matches your criteria, but we do have several homes you’ll love&mdash;all within your budget!</p>
          <p id="results-message" className={resMsgClass}>Showing {resNum} residences {collNum}.</p>
          <div id="results-carousel">
            {showResults}
            <a role="button" className={`arrow left ${prevClass}`} onClick={this.prevSlide}>{ChevronIcon}</a>
            <a role="button" className={`arrow ${nextClass}`} onClick={this.nextSlide}>{ChevronIcon}</a>
          </div>
          {showEmailResults ? emailResults : null}
        </div>
      </div>
    );
  }
}

export default HomeFinder;


