import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import IdleMonitor from 'react-simple-idle-monitor';

import routes from '../../routes';
import Menu from '../../components/Menu';

// import Logo from '../../img/logo.svg';

import './index.css';

class App extends Component {
  constructor() {
    super();
    this.state = {
      showRegister: true,
      rotate: false
    };
  }

  getShowRegister = (state) => {
    this.setState({showRegister: state});
  }

  handleShowRegister = () => {
    this.setState({showRegister: true});
    sessionStorage.clear();
  }

  handleRotate = () => {
    this.setState({ rotate: !this.state.rotate });
  }

  render() {
    var showRegister = this.state.showRegister;

    return (
      <Router>
        <div id="app">
          {/* <IdleMonitor
            timeout={120000}
            onIdle={this.handleShowRegister}
          />
          {
            showRegister
            ?
            <Register handleShowRegister={this.getShowRegister} />
            : */}
            <div id="inner-wrap">
              <div id="main-wrap">
                <div id="print-header">
                  {/* <img className="print-logo" src={Logo} alt="Novel Park"></img> */}
                  <h2>HomeFinder Results</h2>
                  <p>The results are in! Here’s a list of new homes at Novel Park<br/>that are a perfect fit for you.</p>
                </div>
                {routes.map((route, index) => (
                  <Route
                    key={index}
                    path={route.path}
                    exact={route.exact}
                    render={(routeProps) => <route.component {...routeProps} showRegister={showRegister} rotate={this.state.rotate} {...route}/>}
                  />
                ))}
              </div>
              <Menu routes={routes} rotate={this.state.rotate} />
            </div>
          {/* } */}
        </div>
      </Router>
    );
  }
}

export default App;



