import React, { Component } from 'react';
import PropTypes from 'prop-types';

import FlexLink from '../FlexLink';
import CloseIcon from '../icons/CloseIcon';

import './index.css';

class ResidenceResult extends Component {
  static propTypes = {
    collection: PropTypes.string,
    residence: PropTypes.string,
    price: PropTypes.string,
    sqftLow: PropTypes.number,
    sqftHigh: PropTypes.number,
    beds: PropTypes.number,
    budgetQualifier: PropTypes.string,
    budget: PropTypes.number,
    rendering: PropTypes.string,
    useAnchors: PropTypes.bool,
    index: PropTypes.number,
    handleRemoveItem: PropTypes.func
  }

  sqFtToStr = (num) => {
    return num.toString().length > 3 ? num.toString().substring(0,1) + ',' + num.toString().substring(1,4) : num;
  }

  // Send data to parent component
  removeItem = (e, i) => {
    e.preventDefault();
    this.props.handleRemoveItem(i);
  }

  render() {
    const { collection, residence, price, sqftLow, sqftHigh, beds, rendering, useAnchors, index } = this.props;
    const sqFt = sqftLow === sqftHigh ? this.sqFtToStr(sqftHigh) : this.sqFtToStr(sqftLow) + ' – ' + this.sqFtToStr(sqftHigh);
    const bedsLabel = beds > 1 ? 'Bedrooms' : 'Bedroom';
    const collSlug = collection.toLowerCase();
    const sessionBudget = sessionStorage.getItem('budget');

    return (
      <FlexLink className={`result ${collSlug}`} to={`/collections/${collSlug}/residence-${residence}`} anchors={useAnchors}>
        {sessionBudget !== 'choose' ? <span className="icon-close" onClick={(e) => this.removeItem(e, index)}>{CloseIcon}</span> : null}
        <img className="rendering" src={rendering} alt={residence} />
        <h2>{collection}</h2>
        <p className="residence">Residence {residence}</p>
        <p>{sqFt} Sq. Ft.</p>
        <p>{beds} {bedsLabel}</p>
        <p>{price}</p>
      </FlexLink>
    )
  }
}

export default ResidenceResult;


