import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

class MenuLink extends Component {
  render() {
    return <li>
            <NavLink to={this.props.to} children={this.props.children} exact={this.props.exact} activeClassName="active" className={this.props.children.type ? 'img' : ''}/>
          </li>;
  }
}

export default MenuLink;



