import React, { Component } from 'react';

import Carousel from 'nuka-carousel';

import ChevronIcon from '../../components/icons/ChevronIcon';
import ArrowIcon from '../../components/icons/ArrowIcon';

import TheAmenities from '../../img/amenities/the-amenities.jpg';
import GreatSchools from '../../img/amenities/great-schools.jpg';
import GallonsOfFun from '../../img/amenities/gallons-of-fun.jpg';
import AcresOfParks from '../../img/amenities/acres-of-parks.jpg';
import MilesOfTrails from '../../img/amenities/miles-of-trails.jpg';

import './index.css';

class Amenities extends Component {
  constructor(props) {
    super(props);

    this.state = {
      amenities: [],
      slideIndex: 0
    }

    this.images = [
      {
        name: "the-amenities",
        img: TheAmenities
      },
      {
        name: "great-schools",
        img: GreatSchools
      },
      {
        name: "gallons-of-fun",
        img: GallonsOfFun
      },
      {
        name: "acres-of-parks",
        img: AcresOfParks
      },
      {
        name: "miles-of-trails",
        img: MilesOfTrails
      }
    ]
  }

  componentDidMount() {
    let amenities = sessionStorage.getItem('amenities');

    if (amenities) {
      amenities = JSON.parse(amenities);
      this.setState({ amenities });
    } else {
      fetch('https://novelpark.lyonhomes.com/wp-json/wp/v2/pages/41')
      .then(response => {
        return response.json();
      })
      .then(data => {
        const setAm = this.setAmenities(data);
        this.setState({
          amenities: setAm
        });
        sessionStorage.setItem('amenities', JSON.stringify(setAm));
      });
    }
  }

  setAmenities = (data) => {
    let arr = [];

    let main = {};
    main.title = "The amenities.";
    main.subtitle = data.acf.heading;
    main.copy = data.acf.lead_copy;
    arr.push(main);

    if (data.acf.content_blocks && data.acf.content_blocks.length > 0) {
      data.acf.content_blocks.map(item => {
        if (item.title !== '') {
          let block = {};
          block.title = item.title;
          block.copy = item.copy;
          return arr.push(block);
        }
        return null;
      })
    }
    return arr;
  }

  renderAmenities = () => {
    const { amenities } = this.state;

    if (amenities.length > 0) {
      const titles = amenities.map(item => item.title.replace('.',''));

      return amenities.map((item, i) => {
        const subArr = item.subtitle ? item.subtitle.split('?') : null;
        const subArr2 = subArr !== null ? subArr[0].split('.') : null;
        const imgSlug = item.title.split(' ').join('-').toLowerCase().replace('.', '');
        const srcArr = this.images.map(a => a.name === imgSlug ? a.img : null );
        const imgSrc = srcArr.find(s => s !== null);
        const titleArr = item.title.split(' ');
        console.log('titleArr: ', titleArr);
        
        return item.title.length > 0 && item.copy.length > 0
        ?
        <div className="amenity" key={i}>
          <div className="amenity-copy">
            <h2>
              <span className="first">{titleArr[0]}</span>
              {titleArr[1]}
              {titleArr[2] ? ` ${titleArr[2]}` : null}
            </h2>
            {
              subArr2 !== null
              ?
              <h3>
                {`${subArr2[0]}.`}<br/>
                {`${subArr2[1]}.`}<br/>
                {`${subArr2[2]}?`}
              </h3>
              :
              null
            }
            <p>{item.copy}</p>
            <p className="advance-slider" onClick={this.nextSlide}>{i === amenities.length - 1 ? titles[0] : titles[i+1]}. {ArrowIcon}</p>
          </div>
          <img className="amenity-img" src={imgSrc} alt={item.title} />
        </div>
        :
        null
      });
    }
  }

  nextSlide = () => {
    const { amenities, slideIndex } = this.state;
    const newIndex = slideIndex === (amenities.length - 1) ? 0 : slideIndex + 1 ;
    this.setState({ slideIndex: newIndex });
  }

  prevSlide = () => {
    const { amenities, slideIndex } = this.state;
    const newIndex = slideIndex === 0 ? amenities.length - 1 : slideIndex - 1 ;
    this.setState({ slideIndex: newIndex });
  }

  render() {
    const { slideIndex } = this.state;
    const slides = this.renderAmenities();

    return (
      <div id="amenities" className="main-container">
        <Carousel
          className="amenities-slider"
          slidesToShow={1}
          slidesToScroll={1}
          slideIndex={slideIndex}
          withoutControls
          transitionMode="fade"
          speed={750}
          dragging={false}
          wrapAround
        >
          {slides}
        </Carousel>
        <div id="slider-nav">
          <button className="amenity-arrow left" onClick={this.prevSlide}>{ChevronIcon}</button>
          <button className="amenity-arrow" onClick={this.nextSlide}>{ChevronIcon}</button>
        </div>
      </div>
    );
  }
}

export default Amenities;



